@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Work+Sans&display=swap);
.ant-card-hoverable:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: "-2px -2px 4px #faffff, 2px 2px 4px #adbfd5";
  -webkit-transition: all 400ms ease-in-out;
  transition: all 400ms ease-in-out;
}

.NavbarItems {
  display: flex;
  justify-content: flex-end;
  background: #e1effc;
}

.ant-layout-header {
  padding: 0 100px !important;
}

.ant-menu {
  padding: 1rem 0 !important;
  border: none !important;
}

.ant-menu-item {
  margin: 0 0.7rem !important;
  padding: 0.1rem 2rem !important;
  box-shadow: -2px -2px 6px #faffff, 2px 2px 6px #adbfd5;
  background: #dfeffc;
  border: none !important;
  font-size: 1rem;
}

.ant-menu-item:hover,
.ant-menu-item.ant-menu-item-selected,
.ant-menu-item.ant-menu-item-active {
  border: none !important;
  box-shadow: inset -2px -2px 6px #faffff, inset 2px 2px 6px #adbfd5;
}

.ant-menu-item a {
  background: transparent;
  font-weight: 600;
}

.ant-menu-item-selected > a,
.ant-menu-item-active a,
a:hover {
  color: #677790 !important;
}

.header,
.footer {
  height: 3rem;
  line-height: 3rem;
}

.ant-layout,
.ant-layout * {
  background: transparent;
  color: #677790;
  border-radius: 10px;
}

* {
  font-family: "Open Sans", sans-serif;
}

body {
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  color: #677790;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.ant-card-bordered {
  border: none !important;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: scale(2);
            transform: scale(2);
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: scale(2);
            transform: scale(2);
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media (max-width: 960px) {
  .chartjs-render-monitor {
    height: 350px !important;
  }
}

@media (max-width: 780px) {
  .ant-layout-header {
    padding: 0 1.5em !important;
    display: flex;
    justify-content: space-between;
  }

  .ant-menu-submenu-title {
    box-shadow: -2px -2px 6px #faffff, 2px 2px 6px #adbfd5;
  }

  .ant-menu-horizontal > .ant-menu-item:hover,
  .ant-menu-horizontal > .ant-menu-submenu:hover,
  .ant-menu-horizontal > .ant-menu-item-active,
  .ant-menu-horizontal > .ant-menu-submenu-active,
  .ant-menu-horizontal > .ant-menu-item-open,
  .ant-menu-horizontal > .ant-menu-submenu-open,
  .ant-menu-horizontal > .ant-menu-item-selected,
  .ant-menu-horizontal > .ant-menu-submenu-selected {
    border-bottom: none;
  }

  .ant-menu .ant-menu-sub .ant-menu-vertical li:not(:first-child) {
    margin-top: 1em;
  }

  .ant-menu {
    width: 0 !important;
  }

  .ant-row > div:not(:first-child) {
    margin-top: 2em;
  }
}

@media (max-width: 500px) {
  .chartjs-render-monitor {
    height: 300px !important;
    width: 330px !important;
  }
}

@media (max-width: 400px) {
  .chartjs-render-monitor {
    height: 300px !important;
    width: 270px !important;
  }

  .home-mobile-padding {
    padding: 0.5em 1.5em !important;
  }
}

